import { mapGetters } from 'vuex'

export const playlistMixin = {
  computed: {
    ...mapGetters([
      'playList'
    ])
  },
  // document.read
  mounted() {
    this.handlePlaylist(this.playList)
  },
  // 
  activated() {
    this.handlePlaylist(this.playList)
  },
  watch: {
    playList(newVal) {
      this.handlePlaylist(newVal)
    }
  },
  methods: {
    // handlePlaylist
    handlePlaylist() {
      throw new Error('component must implement handlePlaylist method') // handlePlaylist
    }
  }
}


